import React from 'react'
import DetailsCard from "../../../components/personalDetails/personslDetailsCard";
import {translate} from "../../../utils/translate"
import profile from "../../../assets/FACEBOOK_LINE-01-512.png"
import classes from "../../Riders/Tabs/tabs.module.css"
import Avatar from '@material-ui/core/Avatar';
import config from "../../../config/backendURLS"
const VehiclesDrivers = (props) => {
    const {drivers}=props.vehicle
    return (
            
            <>
                  {drivers && drivers.length>0 && drivers.map(driver=>{
                      return <DetailsCard style={{margin:"5px 0"}} key={driver.id}>
                                <div style={styles.profilePhoto}>
                                    <Avatar src={driver && driver.profile_image?`${config.hostDomain}${driver.profile_image}`:profile} alt="profile pic"  style={{width:75,height:75}}/>
                                </div>
                                <div className={classes.info}>
                                    <div className={classes.part1}>
                                    <div style={{display:"flex"}}>
                                        <h4 style={{margin:0,fontWeight:"600"}}>{driver && driver.full_name}</h4>
                                    </div>
                                    <div style={{display:"flex",alignItems:"center"}}>
                                            <h5 style={{margin:"0 3px"}}>{translate("THE_NUMBER")}:</h5>
                                        <h5 style={{direction:"ltr",margin:0}}>{driver && driver.phone_number}</h5>
                                    </div>
                                    </div>
                                    
                                    <div style={styles.part2}>
                                    <div style={{display:"flex",alignItems:"center"}}>
                                            <h5 style={{margin:"0 3px"}}>{translate("RATING")}:</h5>
                                        <h5 style={{margin:0}}>{driver && driver.rating}</h5>
                                        </div>
                                        <div style={{display:"flex",alignItems:"center"}}>
                                            <h5 style={{margin:"0 3px"}}>{translate("GENDER")}:</h5>
                                        <h5 style={{margin:0}}>{translate(driver && driver.gender)}</h5>
                                        </div>
                                        <div style={{display:"flex",alignItems:"center"}}>
                                            <h5 style={{margin:"0 3px"}}>{translate("STATUS")}:</h5>
                                        <h5 style={{margin:0}}>{translate(driver && driver.verification_status)}</h5>
                                        </div>
                                    </div>
                                
                                </div>
                            </DetailsCard>
                            })}
                              {drivers && drivers.length===0 ?
                                <div style={{display:"flex",justifyContent:"center"}}>
                                    <h4>لا يوجد سائقين.</h4>
                                </div>:null 
                            }
                    </>
    )
}


const styles = {
    container:{
        display:"flex",
      
    },
    profilePhoto:{
        display:"flex",
        flexBasis:"15%",
    },
    info:{
        display:"flex",
        flexBasis:"85%"
    },
    part2:{
        display:"flex",
        justifyContent:"space-evenly",
        flexBasis:"64%",
        alignItems: "flex-end",
        flexWrap: "wrap"
    }
}

export default VehiclesDrivers
